
.create-service-page {
  max-width: 1000px;
  margin: 0 auto;
  padding: 40px;
  background-color: #fff;
  font-family: 'Inter', sans-serif;
}

.back-btn {
  color: #493CAE;
  background: none;
  border: none;
  font-weight: 600;
  cursor: pointer;
  margin-bottom: 20px;
}
.two-column-row {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin-bottom: 20px;
}

.form-left,
.form-right {
  flex: 1;
  min-width: 300px;
}

.form-right {
  max-width: 400px;
}
.form-container {
  display: flex;
  flex-direction: column;
  background-color: #F5F5F5;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.form-title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #000;
}
.label-with-tooltip {
  display: flex;
  align-items: center;
  gap: 8px;
}

.tooltip-icon {
  font-size: 14px;
  cursor: help;
  color: #999;
  user-select: none;
}
.input-label {
  font-weight: 600;
  font-size: 14px;
  color: #6D758F;
}

.input, .textarea {
  background: #FFFFFF;
  border: 1px solid #F1F3F7;
  box-shadow: 0px 1px 4px rgba(25, 33, 61, 0.08);
  border-radius: 6px;
  padding: 12px;
  font-size: 14px;
  width: 100%;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 20px;
}

.textarea {
  resize: vertical;
  min-height: 80px;
}

.submit-btn {
  margin-top: 30px;
  background-color: #493CAE;
  color: #fff;
  border: none;
  padding: 14px 20px;
  font-size: 16px;
  font-weight: 600;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.submit-btn:hover {
  background-color: #3b2e94;
}
